.page-header__toggle {
  display: block;
  box-sizing: border-box;
  padding: 0;
  border: none;
  background-color: transparent;
}
.page-header__nav--closed .page-header__toggle {
  background-image: url("../img/menu-open-icon.svg");
  width: 50px;
  height: 24px;
}
.page-header__nav--closed .page-header__toggle:hover,
.page-header__nav--closed .page-header__toggle:focus {
  background-image: url("../img/menu-open-icon-red.svg");
}
.page-header__nav--closed .page-header__toggle:active {
  opacity: 0.3;
}
.page-header__nav--opened .page-header__toggle {
  background-image: url("../img/menu-close-icon.svg");
  width: 23px;
  height: 23px;
}
.page-header__nav--opened .page-header__toggle:hover,
.page-header__nav--opened .page-header__toggle:focus {
  background-image: url("../img/menu-close-icon-red.svg");
}
.page-header__nav--opened .page-header__toggle:active {
  opacity: 0.3;
}
.page-header__nav--closed .page-header__button {
  width: 50px;
  height: 24px;
}
.page-header__nav--opened .page-header__button {
  width: 23px;
  height: 23px;
}
@media (max-width: 1199px) {
  .page-header__nav--closed .page-header__nav-list-item:not(:first-of-type) {
    display: none;
  }
  .page-header__nav--nojs .page-header__toggle {
    display: none;
  }
  .page-header__nav--nojs .page-header__nav-list-item:not(:first-of-type) {
    display: block;
    background-color: #283645;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), inset 0 -1px 0 #000000;
  }
