    position: absolute;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    padding-top: 21px;
    padding-bottom: 21px;
  }
  .page-header__nav--opened .page-header__nav-list-item {
    background-color: #283645;
  }
