    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), inset 0 -1px 0 #000000;
  }
  .page-header__nav--opened .page-header__nav-list-item:first-of-type {
    box-shadow: inset 0 -1px 0 #000000;
    padding-top: 23px;
    padding-bottom: 24px;
  }
  .page-header__nav--opened {
    margin-bottom: -66px;
  }
